<style scoped>
  html {
    font-size: 0.8vw !important;
    font-family: "Poppins", Arial, Tahoma, sans-serif;
    background: #64402c;
  }
  .wrap {
    width: 100%;
    background-size: cover;
    padding: 3vw;
    background: #64402c;
  }
  .wrap .wrap-main {
    width: 60vw;
    height: 10000px;
    margin: 0 auto;
    background: #000;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, .06);
  }
  .wrap .wrap-main .head {
    width: 60vw;
    height: 5vw;
    line-height: 5vw;
    background: rgb(225, 216, 209);
    padding-left: 3vw;
    position: fixed;
    left: 50%;
    margin-left: -30vw;
    z-index: 999;
  }
  .headShow {
    top: 0 !important;
    background: rgba(225, 216, 209, 0.8)!important;
    transition: all 0.1s linear;
  }
  .headHide {
    top: 3vw !important;
    transition: all 0.3s linear;
  }
  .head>img {
    width: 5vw;
    height: 5vw;
  }
  .head .right>li {
    margin-right: 5vw;
    font-size: 1vw;
    color: #929292;
    font-weight: bold;
  }
  .head .right>li>a:hover {
    color: #64402c;
  }
  .navSel {
    color: #64402c;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    margin: 0;
  }
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>

<template>
  <section class="wrap" style="background-image:url('https://themes.muffingroup.com/be/wildlife/wp-content/uploads/2017/02/home_wildlife_bg.jpg');   background-repeat: no-repeat;">
    <section class="wrap-main">
      <div class="head f fc fj" :class="vm.headBack==true?'headShow':'headHide'">
        <img src='http://kaaden.orrzt.com/static/image/微信图片_20180727120626.png' />
        <ul class="right f fc">
          <li v-for="(item,index) in navLst" v-cloak>
            <a :class="{'navSel':vm.selIndex==index}">{{item}}</a>
          </li>
        </ul>
      </div>
      <div style="height:5vw; background: rgb(225, 216, 209);" />
      <!-- 轮播图 -->
      <el-carousel height="30vw">
        <el-carousel-item v-for="item in 4" :key="item">
          <h3>{{ item }}</h3>
        </el-carousel-item>
      </el-carousel>
    </section>
    <!-- <section class="head f fc fs rv">
                                                <div class="left logotxt rel">
                                                  <img src='http://kaaden.orrzt.com/static/image/微信图片_20180727120626.png' class="leftLogo" />
                                                </div>
                                                <ul class="rightNav f fc">
                                                  <li v-for="(item,index) in navLst" v-cloak>
                                                    <a :class="{'navSel':selIndex==index}">{{item}}</a>
                                                  </li>
                                                </ul>
                                              </section>
                                              <section class="main rv" style=" background-image: url('https://themes.muffingroup.com/be/company3/wp-content/uploads/2018/06/home_company3_slider_pic5.png');">
                                              </section> -->
  </section>
</template>

<script>
  export default {
    name: 'index',
    data() {
      return {
        navLst: ['首页', '事迹', '工作', '联系方式'],
        vm: {
          selIndex: 0,
          headBack: false,
        }
      }
    },
    methods: {
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        if (scrollTop == 0) {
          this.vm.headBack = false
        } else {
          this.vm.headBack = true
        }
        console.log(this.vm.headBack)
      },
    },
    created: function() {},
    mounted() {
      window.addEventListener('scroll', this.handleScroll)
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll)
    },
  }
</script>





