<style scoped>
    .wrap-box {
        width: 100%;
        height: 100vh;
        background: #64402c;
    }
    .login-box {
        width: 40vw;
        height: 70vh;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-left: -20vw;
        margin-top: -35vh;
        box-shadow: 0 0 30px rgba(0, 0, 0, 0.4);
        border-radius: 10px;
    }
    .left-box {
        width: 20%;
        height: auto;
        background: rgb(255, 255, 255);
    }
    .left-box img {
        width: 5vw;
        height: 5vw;
    }
    .left-box p {
        width: 100%;
        font-size: 0.4vw;
        position: absolute;
        bottom: 1vw;
        text-align: center;
    }
    .right-box {
        width: 80%;
        height: auto;
        background: rgb(225, 216, 209);
        padding: 5vw 0;
    }
    .right-box .top {
        width: 7vw;
        height: 2vw;
    }
    .right-box .top p {
        width: 50%;
        height: 2vw;
        line-height: 2vw;
        text-align: center;
        cursor: pointer;
    }
    .right-top {
        border-top-left-radius: 1vw;
        border-bottom-left-radius: 1vw;
    }
    .right-down {
        border-top-right-radius: 1vw;
        border-bottom-right-radius: 1vw;
    }
    .sel-box {
        color: #fff;
        background: #475669;
    }
    .selno-box {
        background: #fff;
        color: #475669;
    }
    .login {
        width: 100%;
        color: #333;
        padding: 0 7vw;
        margin-top: 3vw;
        font-size: 0.7vw;
    }
    .login li {
        margin-bottom: 2vw;
    }
    .login span {
        margin-bottom: 1vw;
    }
    .login input {
        border: none;
        background: none;
        border-bottom: 1px solid #333;
        padding: 0.5vw 1vw;
    }
    .login .check-login {
        position: absolute;
        bottom: 0;
        right: 0;
    }
    .login .check-login img {
        width: 6vw;
        height: 2.5vw;
    }
    .login .goBtn {
        width: 100%;
        height: 2.5vw;
        background: #475669;
        color: #fff;
        font-size: 1vw;
        font-weight: bold;
        line-height: 2.5vw;
        text-align: center;
        margin-top: 5vw;
        border-radius: 3px;
    }
</style>

<template>
    <section class="wrap-box" style="background-image:url('https://themes.muffingroup.com/be/wildlife/wp-content/uploads/2017/02/home_wildlife_bg.jpg');   background-repeat: no-repeat;">
        <section class="login-box f rv">
            <div class="left-box f fc fc-h rel">
                <img src='http://kaaden.orrzt.com/static/image/微信图片_20180727120626.png' />
                <p>Since by 2018.8.6</p>
            </div>
            <div class="right-box f fv fc">
                <div class="top f fc">
                    <p class="right-top" :class="[{'sel-box':vm.sel==0,'selno-box':vm.sel==1}]" v-cloak @click="changeSel(0)">登陆</p>
                    <p class="right-down" :class="[{'sel-box':vm.sel==1,'selno-box':vm.sel==0}]" v-cloak @click="changeSel(1)">注册</p>
                </div>
                <!-- 登陆 -->
                <div v-if="vm.sel==0" class="login">
                    <ul>
                        <li class="f fv">
                            <span>账号</span>
                            <input v-model="vm.account">
                        </li>
                        <li class="f fv">
                            <span>密码</span>
                            <input type="password" v-model="vm.password">
                        </li>
                        <li class="f fv rel">
                            <span>验证码</span>
                            <input style="width:12vw;" v-model="vm.iden">
                            <a href="#" rel="external nofollow" @click="editCaptcha" class="check-login">
                                <img src="http://kaaden.orrzt.com/blogIden" alt="" ref="imgYzm">
                            </a>
                        </li>
                        <div class="goBtn" @click="login">确定</div>
                    </ul>
                </div>
            </div>
        </section>
    </section>
</template>

<script>
    import {
        http,
        addr,
        tools,
    } from "@/assets/util.js";
    export default {
        name: 'login',
        data() {
            return {
                vm: {
                    sel: 0,
                    password: "",
                    account: "",
                    iden: "",
                }
            }
        },
        methods: {
            changeSel(index) {
                this.vm.sel = index
            },
            editCaptcha() {
                this.$refs.imgYzm.src = 'http://kaaden.orrzt.com/blogIden?d=' + Math.random()
            },
            login() {
                let vm = this.vm
                if (vm.account == '') {
                    tools.showError("请填写账号", this)
                    return;
                }
                if (vm.password == '') {
                    tools.showError("请填写密码", this)
                    return;
                }
                if (vm.iden == '') {
                    tools.showError("请填写验证码", this)
                    return;
                }
                http.post(addr.blogLogin, {
                    vm,
                }).then(data => {
                    if (data.isok) {} else {
                        tools.showError(data.msg, this)
                        this.$refs.imgYzm.src = 'http://kaaden.orrzt.com/blogIden?d=' + Math.random()
                    }
                    console.log(data)
                })
            },
        },
        created: function() {},
        mounted() {},
        destroyed() {},
    }
</script>





