import $ from 'jquery'
const host = 'http://kaaden.orrzt.com';
// const host="http://localhost:8089"
const addr = {
  loginCheck: host + '/logoCheck', //验证码
  blogLogin:host+"/blogLogin",
};
const http = {
  post: function (url, data) {
    return new Promise(function (resolve, reject) {
      $.ajax({
        url,
        data,
        type: 'POST',
        dataType: 'json',
        contentType: "application/x-www-form-urlencoded; charset=utf-8",
        success: function (data) {
          resolve(data)
        }
      });
    })
  },
  get: function (url, data) {
    return new Promise(function (resolve, reject) {
      $.ajax({
        url,
        data,
        type: 'GET',
        dataType: 'json',
        contentType: "application/x-www-form-urlencoded; charset=utf-8",
        success: function (data) {
          resolve(data)
        }
      });
    })
  }
};
const tools = {
  goNewPage(url,target) {
    target.$router.push(url)
  },
  goBack(target){
    target.$route.back(-1)
  },
  showError(msg,target){
    target.$notify.error({
      title: '错误',
      message:msg
  });
  },
};
export {
  http,
  tools,
  addr,
}



